@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Pro:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Pro:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home{

    display: -webkit-flex;

    display: flex;
    width: 100%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-family: 'Oswald', sans-serif;

}

.Home__left{
    /* margin: 5px 15px 0px 15px; */
    -webkit-flex:4 1;
            flex:4 1;
    display: -webkit-flex;
    display: flex;

    -webkit-flex-direction: column;

            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    /* background-color: #d3d3d3; */

    margin: 0px 0px 0px 10px;
    padding: 25px;

}


.content{
    background-color: #9ee08e;
    border-radius: 20px;
    padding: 10px;
}

.con>h5{
    background-color: #9ee08e;
    border-radius: 20px;
    padding: 10px;
    text-align: center;
}

.list-group>h5{
    text-align: center !important;
    
}

.list-group{
    margin: 20px 0px 15px 0px !important;
    padding: 15px 0px 15px 0px !important;
    background-color: #7ed6ca;
    border-radius: 20px;
}

.Home__middle{
    /* margin: 5px 15px 0px 15px; */
    -webkit-flex:6 1;
            flex:6 1;
    display: -webkit-flex;
    display: flex;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 150px;
    padding: 25px;

}

.Chart{
    height:100%;
    width: 100%;

}


.graphs{
    margin: 20px;
}



@media only screen and (max-width: 991px) {
    .home{
        display: block;
    }

    .Home__middle{
        padding: 0;
    }
    .graphs{
        margin: 30px 0px 30px 0px;
    }


}
.about{
    text-align: center;

}

.cardlist{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 10px;
    border-radius: 20px;
}

.cardinfo{
    width: 18rem;
}


@media only screen and (max-width: 991px) {
    .cardlist{
        display: block;
    
    }
    .cardinfo{
        width: 100%;
    }
    
}
.healthcare{
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 10px 10% 10px 10%;
    font-family: 'Crimson Pro', serif;
    font-size: 18px;

}

.healthcare>h1{
    text-align: center;
}

.select__items{
    width: 400px;
    height: 45px;
    border-radius: 50px;
    
}

.animated__ring{
    position: absolute !important;
    top: 50% !important;
    left: 50% !important; 
    -webkit-transform: translate(-50%, -50%) !important; 
            transform: translate(-50%, -50%) !important;
}

.form-outline{
    margin:20px 30px 20px 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
}


.sec{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    font-size: larger;
    font-weight: 500;
}

.vaccine_show{
    margin: 0px 5% 0px 5%;

}

.vaccine{
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    
}

.vaccine>h1{
    font-weight: 600;
}

@media only screen and (max-width: 991px) {
    .sec{
        display: block;
    }
    
}
.state__color{
    color: blueviolet;
}

.state__codecolor{
    color: rgb(80, 70, 226);
}

.tracker{
    margin: 10px 5% 10px 5%;
    font-family: 'Crimson Pro', serif;
    font-weight: 800;
}

.tracker>h1{
    text-align: center;
}

.state__confirmed{
	color:red;
}

.state__active{
    color: brown;
}
.state__deaths{
    color: blue;
}
.state__recovered{
    color: green;
}
.lastup{
    color: slateblue;
}
